@import "../../../styles/global";

.Benefits {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 64px;
  justify-content: flex-start;

  &__title {
    width: 100%;
  }

  &__title, &__description {
    padding-bottom: $font-size * 2;
  }

  @include over540 {
    &__items {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
      }
    &__description {
      max-width: 500px;
    }
  }

  @include over850() {
    padding-bottom: $section-margin * 2;

    &__description {
      padding-bottom: $font-size * 4;
    }
  }
}